.logo-img {
  height: 42px;
  width: auto;
  display: block;
}

.site-logo img {
  height: 40px;   /* Try 30px or 35px if you want it smaller */
  width: auto;
  display: block;
}

:root {
  --primary: #2d3436;
  --accent: #6c5ce7;
  --text: #2d3436;
  --text-light: #636e72;
  --border: #dfe6e9;
  --bg: #ffffff;
  --bg-alt: #f8f9fa;
}
    
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
    
    body {
      font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
      line-height: 1.7;
      color: var(--text);
      background: var(--bg);
    }
    
    /* Header */
    .site-header {
      background: var(--bg);
      border-bottom: 1px solid var(--border);
      padding: 30px 0;
      position: sticky;
      top: 0;
      z-index: 100;
      backdrop-filter: blur(10px);
      background: rgba(255, 255, 255, 0.95);
    }
    
    .header-container {
      max-width: 1200px;
      margin: 0 auto;
      padding: 0 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    
    .site-logo {
      font-family: 'Lora', serif;
      font-size: 28px;
      font-weight: 700;
      letter-spacing: -0.5px;
    }
    
    .site-logo a {
      color: var(--primary);
      text-decoration: none;
      transition: color 0.3s;
    }
    
    .site-logo a:hover {
      color: var(--accent);
    }
    
    .header-right {
      display: flex;
      align-items: center;
      gap: 30px;
    }
    
    nav ul {
      list-style: none;
      display: flex;
      gap: 35px;
    }
    
    nav a {
      color: var(--text);
      text-decoration: none;
      font-weight: 500;
      font-size: 15px;
      transition: color 0.3s;
      letter-spacing: 0.3px;
    }
    
    nav a:hover {
      color: var(--accent);
    }
    
    /* Search Button */
    .search-toggle {
      background: none;
      border: none;
      cursor: pointer;
      padding: 8px;
      color: var(--text);
      transition: color 0.3s;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    
    .search-toggle:hover {
      color: var(--accent);
    }
    
    /* Search Modal */
    .search-modal {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.8);
      z-index: 1000;
      backdrop-filter: blur(5px);
      animation: fadeIn 0.2s ease;
    }
    
    .search-modal.active {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      padding-top: 100px;
    }
    
    @keyframes fadeIn {
      from { opacity: 0; }
      to { opacity: 1; }
    }
    
    .search-modal-content {
      background: white;
      width: 90%;
      max-width: 700px;
      border-radius: 12px;
      overflow: hidden;
      box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
      max-height: 80vh;
      display: flex;
      flex-direction: column;
      animation: slideDown 0.3s ease;
    }
    
    @keyframes slideDown {
      from {
        transform: translateY(-50px);
        opacity: 0;
      }
      to {
        transform: translateY(0);
        opacity: 1;
      }
    }
    
    .search-header {
      display: flex;
      align-items: center;
      padding: 20px;
      border-bottom: 1px solid var(--border);
    }
    
    #searchInput {
      flex: 1;
      border: none;
      font-size: 18px;
      padding: 10px;
      outline: none;
      font-family: inherit;
      color: var(--text);
    }
    
    #searchInput::placeholder {
      color: var(--text-light);
    }
    
    .search-close {
      background: none;
      border: none;
      font-size: 32px;
      cursor: pointer;
      color: var(--text-light);
      padding: 0 10px;
      line-height: 1;
      transition: color 0.3s;
    }
    
    .search-close:hover {
      color: var(--accent);
    }
    
    .search-results {
      overflow-y: auto;
      max-height: calc(80vh - 80px);
      padding: 20px;
    }
    
    .search-result-item {
      padding: 20px;
      border-bottom: 1px solid var(--border);
      cursor: pointer;
      transition: background 0.2s;
      text-decoration: none;
      display: block;
      color: inherit;
    }
    
    .search-result-item:last-child {
      border-bottom: none;
    }
    
    .search-result-item:hover {
      background: var(--bg-alt);
    }
    
    .search-result-title {
      font-family: 'Lora', serif;
      font-size: 20px;
      font-weight: 700;
      color: var(--primary);
      margin-bottom: 8px;
    }
    
    .search-result-item:hover .search-result-title {
      color: var(--accent);
    }
    
    .search-result-meta {
      font-size: 13px;
      color: var(--text-light);
      margin-bottom: 8px;
    }
    
    .search-result-excerpt {
      color: var(--text-light);
      font-size: 15px;
      line-height: 1.6;
    }
    
    .search-no-results {
      text-align: center;
      padding: 60px 20px;
      color: var(--text-light);
    }
    
    .search-no-results svg {
      width: 64px;
      height: 64px;
      margin-bottom: 20px;
      opacity: 0.3;
    }
    
    mark {
      background: #fff59d;
      padding: 2px 4px;
      border-radius: 2px;
      font-weight: 600;
    }
    
    /* Main Content */
    main {
      max-width: 1200px;
      margin: 0 auto;
      padding: 80px 30px;
      min-height: 60vh;
    }
    
    /* Footer */
    .site-footer {
      background: var(--primary);
      color: white;
      padding: 60px 30px 30px;
      margin-top: 100px;
    }
    
    .footer-container {
      max-width: 1200px;
      margin: 0 auto;
    }
    
    .footer-grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      gap: 50px;
      margin-bottom: 40px;
    }
    
    .footer-section h3 {
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: 600;
    }
    
    .footer-section p {
      color: rgba(255, 255, 255, 0.8);
      line-height: 1.7;
      margin-bottom: 15px;
    }
    
    .footer-section a {
      color: rgba(255, 255, 255, 0.9);
      text-decoration: none;
      display: block;
      margin-bottom: 10px;
      transition: color 0.3s;
    }
    
    .footer-section a:hover {
      color: var(--accent);
    }
    
    .footer-bottom {
      text-align: center;
      padding-top: 30px;
      border-top: 1px solid rgba(255, 255, 255, 0.1);
      color: rgba(255, 255, 255, 0.7);
      font-size: 14px;
    }
    
    /* Responsive */
    @media (max-width: 768px) {
      .header-container {
        flex-direction: column;
        gap: 20px;
      }
      
      .header-right {
        width: 100%;
        justify-content: space-between;
      }
      
      nav ul {
        gap: 20px;
      }
      
      main {
        padding: 50px 20px;
      }
      
      .search-modal.active {
        padding-top: 50px;
      }
    }

/* css for the table */
table {
  width: 100%;
  border-collapse: collapse;
}

table th, table td {
  border: 1px solid #ddd;
  padding: 8px;
}

table th {
  background: #f5f5f5;
  font-weight: bold;
}

